import { useState } from "react";
import Video from "./Video";


const ArchivedVideos = ({data}) => {
    const [maxVideos, setMaxVideos] = useState(4);

    var tempVideoDisp = [];
    var bottomElement = <button className="btn" onClick={clickHandler}>Load More</button>;
  
    for(let i = 0; i < data.length; i++){
        tempVideoDisp.push(data[i]);
        if(tempVideoDisp.length === maxVideos) break;
        if(i === data.length - 1) bottomElement = <p className="transparentText">Looks like you've reached the end</p>;
    }

    // get next page and if there is no next page el then just say bottom shit
    function clickHandler() {
        setMaxVideos(maxVideos + 4);
    }

    // function getNextPage(){
        // fetch(`https://api.vimeo.com/users/${sconaChannelId}/videos?direction=desc&page=2`, {
		// 	method:'GET', 
		// 	headers: {'Authorization': 'Bearer ' + token}
		// })
		// .then(res => res.json())
		// .then(
        //     (result) => {
        //         setVideos(result.data);
        //         setIsLoaded(true);
        //         console.log(result);
        //     },

        //     (error) => {
        //         setIsLoaded(true);
        //         setError(error);
        //         console.log("ERROR: " + error);
        //     }
		// )
    // }

    return(
        <>
            <div className="videoGrid">
                {tempVideoDisp.map((video, i) => (
                    <div key={i} className="videoTitleContainer">
                        <Video title={video.name} videoEmbed={video.embed.html}/>
                        <p>{video.name}</p> 
                    </div>
                ))}
            </div>
            { bottomElement }
        </>
    )	
}

export default ArchivedVideos