import { useState, useEffect } from "react";
import ArchivedVideos from "./ArchivedVideos";
import LiveVideos from "./LiveVideos";

const token = "5d5c157df9f684e811d74a303341da43";
const showcaseId = "7664293";
const sconaChannelId = "82197283";

const VideoSections = () =>{ 
    const [liveError, setLiveError] = useState(null);
	const [isLiveLoaded, setIsLiveLoaded] = useState(false);
	const [liveVideos, setLiveVideos] = useState([]);

    const [showcaseVideos, setShowcaseVideos] = useState([]);
    const [showcaseLoaded, setShowcaseLoaded] = useState(false);
    const [showcaseError, setShowcaseError] = useState(null);

	useEffect(() => {
        fetch(`https://api.vimeo.com/users/${sconaChannelId}/videos?filter=live`, {
            method:'GET', 
            headers: {'Authorization': 'Bearer ' + token}
        })
        .then(res => res.json())
        .then(
            (result) => {
                setLiveVideos(result.data);
                setIsLiveLoaded(true);
            },
            (error) => {
                setIsLiveLoaded(true);
                setLiveError(Object.values(error));
            }    
        )

        // showcases were previously called albums but the API hasn't yet updated
        // this may cause issues in the future when they do eventually update it
        fetch(`https://api.vimeo.com/users/${sconaChannelId}/albums/${showcaseId}/videos`, {
            method:'GET', 
            headers: {'Authorization': 'Bearer ' + token}
        })
        .then(res => res.json())
        .then(
            (result) => {
                setShowcaseVideos(result.data);
                setShowcaseLoaded(true);
                console.log(result);
            },
            (error) => {
                setShowcaseLoaded(true);
                setShowcaseError(error);
            }    
        )
	}, [])

    function getLiveContent(){
        if(liveError){
            return <p className="transparentText">Error: {liveError}</p>;
        } else if(!isLiveLoaded){
            return <p className="transparentText">Loading...</p>;
        } else{
            return <LiveVideos data={liveVideos}/>;
        }
    }

    function getShowcaseContent(){
        if(showcaseError){
            return <p className="transparentText">Error: {showcaseError}</p>;
        }else if(!showcaseLoaded){
            return <p className="transparentText">Loading...</p>;
        }else{
            return <ArchivedVideos data={showcaseVideos}/>;
        }
    }

    return(
        <>
            <h1 className="subtitle">Live Videos</h1>
            { getLiveContent() }

            <h1 className="subtitle">Archived Videos 2020</h1>
            { getShowcaseContent() }
        </>

        /*  if (error) {
                return <div>Error: {error.message}</div>;
            } else if (!isLoaded) {
                return <div>Loading...</div>;
            } else {
                return (
                    <div className="videoGrid">
                        {videos.map((video, i) => (
                            <Video key={i} title={video.name} videoEmbed={video.embed.html}/>
                        ))}
                    </div>
                );
            }
        */
    );
}

export default VideoSections;